body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.headerMain h1 {
  font-size: 35px;
  text-align: left;
  margin-left: 15px;
  color: white;
}
.headerMain {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 30px 0 30px 10px;
  background-color: #023047;
}

.footerMain {
  display: flex;
  margin-top: 30px;
  justify-content: center;
  align-items: center;
  background-color: rgb(10, 10, 10);
  color: white;
  height: 40px;
}

.navbar {
  padding: 15px 5px 15px 5px;
  max-width: 100vw;
  display: flex;
  justify-content: start;
  align-items: center;
  background: linear-gradient(
    90deg,
    rgba(33, 158, 188, 1) 50%,
    rgba(95, 178, 217, 1) 100%
  );
}

.navbarMain {
  flex-grow: 12;
  display: flex;
  justify-content: start;
  align-items: center;
}
.navbarAdmin {
  flex-grow: 1;
  display: flex;
  justify-content: end;
  align-items: center;
}

.navbarMain ul {
  display: flex;
  align-items: center;
  list-style-type: none;
  grid-gap: 12px;
  gap: 12px;
}

.navbarMain ul li a {
  color: white !important;
  font-weight: 500;
}

.navbarMain a {
  display: block;
  text-decoration: none;
  font-size: 17px;
}

.navbarMain li {
  transition: 0.4s;
}

.navbarMain li:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.navbarAdmin {
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.navbarAdmin h5 {
  margin-right: 10px;
  font-size: 15px;
}

.active {
  border-bottom: 2px solid white;
}

.iconNav {
  margin-right: 5px;
}

.adminButton {
  background-color: #fb8500;
  border: 1px solid rgba(27, 31, 35, 0.15);
  border-radius: 6px;
  box-shadow: rgba(27, 31, 35, 0.1) 0 1px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  line-height: 20px;
  max-width: 170px;
  min-height: 50px;
  font-weight: 600;
  border-radius: 4px;
  padding: 8px 16px;
  text-align: center;
  transition: 0.6s;
}

.adminButton:hover {
  background-color: #ffb703;
}

input[type="password"] {
  color: rgb(42, 35, 35);
  font-size: 16px;
  line-height: 20px;
  max-width: 190px;
  min-height: 28px;
  border-radius: 4px;
  padding: 8px 16px;
  border: 2px solid transparent;
  box-shadow: rgb(0 0 0 / 12%) 0px 1px 3px, rgb(0 0 0 / 24%) 0px 1px 2px;
  background: rgb(251, 251, 251);
  transition: 0.4s;
  margin-right: 10px;
}

