th, td {
  min-width: 60px;
  text-align: center;
}
.formMain {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
  padding: 50px 100px 50px 100px;
  margin-bottom: 50px;
  flex-wrap: wrap;
}
.formMain h2 {
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.inputRow1 {
  display: flex;
  grid-gap: 60px;
  gap: 60px;
}
.inputRow2 {
  display: flex;
  grid-gap: 60px;
  gap: 60px;
}
.trashIcon {
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}
.trashIcon:hover {
  -webkit-transform: translate(0px, -3px);
          transform: translate(0px, -3px);
}
input[type="text"], input[type="number"], select {
  color: rgb(42, 35, 35);
  font-size: 16px;
  line-height: 20px;
  min-width: 220px;
  min-height: 28px;
  border-radius: 4px;
  padding: 8px 16px;
  border: 2px solid transparent;
  box-shadow: rgb(0 0 0 / 12%) 0px 1px 3px, rgb(0 0 0 / 24%) 0px 1px 2px;
  background: rgb(251, 251, 251);
  transition: 0.4s;
}
input:focus {
  border: 2px solid rgb(20, 17, 32) !important;
}
label {
  font-size: 18px;
  font-weight: 600;
}
